body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 8vh;
  margin-bottom: 8vh;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation: Logo-fly-up 3s;
          animation: Logo-fly-up 3s;
}

.App-logo-wide {
  height: 8vmin;
  -webkit-animation: Fade-in 5s;
          animation: Fade-in 5s;
}

.App-header {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #0F0929;
}

.Particles-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background: -webkit-radial-gradient(#0F0929e8 1%,#0F0929 40%);
  background: radial-gradient(#0F0929e8 1%,#0F0929 40%);
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  padding-bottom: 6vmin;
}

.Particles {
  position: absolute;
  width: 100%;
  height: 100vh;
  z-index: -1;
}

.Down-arrow-container{
  align-self: flex-end;
  margin-bottom: 6vmin;
  -webkit-animation: Fade-in 8s;
          animation: Fade-in 8s;
}

.Down-arrow{
  height: 6vmin;
  -webkit-animation: Down-arrow-bounce infinite 1.5s linear;
          animation: Down-arrow-bounce infinite 1.5s linear;
}
.Sub-header {
  font-family: "Open Sans Light";
  font-weight: 300;
  color:white;
  -webkit-animation: Fade-in 5s;
          animation: Fade-in 5s;
}

@-webkit-keyframes Down-arrow-bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  80% {
    -webkit-transform: translateY(2vmin);
            transform: translateY(2vmin);
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes Down-arrow-bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  80% {
    -webkit-transform: translateY(2vmin);
            transform: translateY(2vmin);
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes Logo-fly-up {
  0% {
    -webkit-transform: translateY(16vh) scale(0.5,0.5);
            transform: translateY(16vh) scale(0.5,0.5);
  }

  25% {
    -webkit-transform: translateY(16vh) scale(2,2);
            transform: translateY(16vh) scale(2,2);
  }

  50% {
    -webkit-transform: translateY(16vh) scale(2,2);
            transform: translateY(16vh) scale(2,2);
  }

  100% {
    -webkit-transform: translateY(0) scale(1,1);
            transform: translateY(0) scale(1,1);
  }
}

@keyframes Logo-fly-up {
  0% {
    -webkit-transform: translateY(16vh) scale(0.5,0.5);
            transform: translateY(16vh) scale(0.5,0.5);
  }

  25% {
    -webkit-transform: translateY(16vh) scale(2,2);
            transform: translateY(16vh) scale(2,2);
  }

  50% {
    -webkit-transform: translateY(16vh) scale(2,2);
            transform: translateY(16vh) scale(2,2);
  }

  100% {
    -webkit-transform: translateY(0) scale(1,1);
            transform: translateY(0) scale(1,1);
  }
}

@-webkit-keyframes Fade-in {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100%{
    opacity: 100%;
  }
}

@keyframes Fade-in {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100%{
    opacity: 100%;
  }
}
